*{
    
  box-sizing:border-box;
  margin: 0;
  padding:0;
  background-color: #CCFBF6;
  font-family: Georgia, serif;
  font-size: 20px;
  letter-spacing: 2px;
  word-spacing: 2px;
  color: #000000;
  font-weight: normal;
  text-decoration: none;
  font-style: normal;
  font-variant: small-caps;
  text-transform: none;
  scroll-behavior: smooth;
}
#logo{
font-family: Georgia, serif;
font-size: 30px;
letter-spacing: 2.5px;
word-spacing: 2px;
color: #000000;
font-weight: normal;
text-decoration: none;
font-style: normal;
font-variant: small-caps;
text-transform: none;
}

/* HomePage */
#homePicture{
  padding-top:3px;
  height:650px;
  width:1450px;
}
.homeText1{
  color: #606060;
  font-size: 40px;
  padding-left:200px; 
}

.col-md-4 {
  flex: 0 0 33.3333333%;
  max-width: 33.3333333%;
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  display: flex;
}

/* Quotes */

#quotesImage1{
  height: 300px;
  width: 400px;
 
}

/* Diary */
button {
  display: block;
  font-size: 1.2rem;
  padding: 12.5px;
  border-style: double;
  border-color: #7BCAE9;
  background-color: #CCFBF6;
  color: #7BCAE9;
  
}
header {
  text-align: center;
  margin: 1em auto;
}
.section {
  padding: 1em 1em;
  width: 100%;
  margin: 1em auto;
}
.container {
  width: 100%;
  margin: 0 auto;
  padding: 1em 1em;
}

.container-row {
  width: 90%;
  margin: 0 auto;
}

/* Button Standard */

.btn-main,
.btn-light,
.btn-dark {
  display: block;
  padding: 0.6rem 2rem;
  margin: 1rem;
  transition: all 0.1s;
  border: none;
  border-radius: 2px;
  cursor: pointer;
}

.btn-main {
  color: lightgoldenrodyellow;
  background-color: lightcoral;
}

.btn-light {
  color: #0a2472;
  background-color: white;
}

.btn-dark {
  color: white;
  background-color: #0a2472;
}

button[class^='btn-']:hover,
a[class^='btn-']:hover,
input[class^='btn-']:hover {
  background-color: lightgoldenrodyellow;
  color: lightcoral;
  font-weight: 700;
  transform: scale(1.08);
}

/* Project Starts Here */

/* Journal Entry Section */

/* Title */

.entry-text-title {
  width: 20em;
  margin: 1em auto;
  padding: 1em;
  border-radius: 2px;
  border: unset;
  background-color: lightgoldenrodyellow;
 
}

.entry-text-title:focus {
  outline-color: lightcoral;
 
  color: lightcoral;
  font-size: 1rem;
}

.entry-text-title::placeholder {
  color: lightcoral;

  font-size: 1rem;
}

.entry-text-title::-ms-value {
  color: lightcoral;
  font-size: 1rem;
}

/* Text Box */

.container-row-journal {
  display: flex;
  justify-content: center;
}

.entry-form {
  max-width: 100%;
  margin: 0 auto;
}

.journal-label {
  display: block;
  line-height: 1.6;
  font-size: 1.2rem;
  font-weight: 700;
}

.entry-text-box {
  width: 100%;
  height: 20em;
  margin: 1em auto;
  padding: 1em;
  border-radius: 2px;
  border: unset;
  background-color: lightgoldenrodyellow;
}

@media (min-width: 769px) {
  .entry-text-box {
    width: 40em;
    height: 20em;
    margin: 1em auto;
    padding: 1em;
    border-radius: 2px;
    border: unset;
    background-color: lightgoldenrodyellow;
    
  }
}

.entry-text-box:focus {
  outline-color: lightcoral;

  color: lightcoral;
  font-size: 1rem;
}

.entry-text-box::placeholder {
  color: lightcoral;

  font-size: 1rem;
}

.entry-submit-btn {
  margin: 0;
}




